.carousel-small {
    margin-bottom: -0.2rem;
    margin-top: -0.4rem;
    @include iphone {
        margin-top: -0.9rem;
    }
    @include laptop {
        margin-top: 0;
    }
    &--height-345 {
        height: 21.5rem;
    }
    &--img-height-345 {
        height: 23.5rem;
        @include iphone {
            height: 23.5rem;
        }
        margin-top: -21.5rem;
    }
    &--height-510 {
        height: 33rem;
    }
    &--img-height-510 {
        height: 33.5rem;
        margin-top: -33rem;
    }
    &__card {
        width: 80%;
        padding-left: 0;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        margin-top: 0;
        @include iphone {
            justify-content: unset;
        }
        @include laptop {
            margin-top: 1.1rem;
        }
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        position: relative;
    }
    &__centered {
        padding-left: 0;
        justify-content: center;
    }
    &__heading-large {
        font-size: 4.1rem;
        padding: 1.5rem 3rem 1.8rem 3rem;
        @include iphone {
            font-size: 6.5rem;
            padding: 1.9rem 4rem 2.5rem 4rem;
        }
        background: rgba(41, 38, 99, 0.76);
        line-height: 3.3rem;
        color: $white;
    }
    &__heading {
        font-size: 1.48rem;
        color: $white;
        padding: 1rem 2.5rem 1.3rem 2.5rem;
        line-height: 1.9rem;
        text-align: center;
        margin-top: 2rem;
        @include iphone {
            font-size: 2.5rem;
            line-height: 2.9rem;
        }
        @include laptop {
            font-size: 3.1rem;
            padding: 1rem 4rem 1.4rem 4rem;
            line-height: 3.3rem;
        }
        background: rgba(41, 38, 99, 0.76);
    }
    &__heading-alt {
        font-size: 1.6rem;
        font-family: $footer-font-family;
        color: $white;
        padding: 1rem 3rem 1.3rem 3rem;
        line-height: 1.9rem;
        text-align: center;
        @include iphone {
            font-size: 2.2rem;
            line-height: 2.9rem;
        }
        @include laptop {
            font-size: 2.2rem;
            padding: 1rem 4rem 1.4rem 4rem;
            line-height: 3.3rem;
        }
        background: rgba(41, 38, 99, 0.76);
    }
    &__img {
        // height: 21.1rem;
        width: 100%;
        object-fit: cover;
        // @include iphone {
        //     height: 21.1rem;
        // }
    }
}