.footer {
  background: $secondary-color;
  padding: 3.4rem 0 0 0;
  color: $white;
  &__layout {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 0.75rem;
    @include tablet {
      flex-wrap: nowrap;
      padding: 0;
    }
    & a {
      color: $white;
    }
    & ul li {
      color: $white;
    }
  }
  &__logo {
    font-family: $footer-font-family;
    color: $white;
    font-size: 1.53rem;
    padding: 0 0 0.65rem 0;
    @include tablet {
      padding: 0 0 0.3rem 0;
    }
  }
  &__strapline {
    color: $white;
    margin-bottom: 0.8rem;
    @include tablet {
      margin-bottom: 0;
    }
  }
  &__col:nth-child(2) {
    font-size: 0.79rem;
    width: 100%;
    margin-left: 0%;
    @include iphone {
      font-size: 0.84rem;
    }
    @include tablet {
      font-size: 0.84rem;
      width: 48%;
      margin-left: 8%;
    }
    @include laptop {
      font-size: 0.87rem;
      width: 46%;
      margin-left: 8%;
    }
  }
  &__col:nth-child(1) {
    font-size: 0.79rem;
    margin-right: 0;
    width: 100%;
    @include iphone {
      font-size: 0.84rem;
    }
    @include tablet {
      font-size: 0.84rem;
      margin-right: 1rem;
      width: 44%;
    }
    @include laptop {
      font-size: 0.87rem;
      margin-right: 1rem;
      width: 46%;
    }
  }
  &__col-fluid {
    text-decoration: none;
  }
  &__col-fluid:hover {
    text-decoration: underline;
  }
  &__icon-list {
    margin-top: 0.8rem;
    position: relative;
    margin-left: 2.6rem;
    @include tablet {
      margin-left: 2.45rem;
    }
  }
  &__icon-li {
    margin-bottom: 1.3rem;
    line-height: 1.15rem;
    @include iphone {
      line-height: 1.25rem;
    }
  }
  &__icon-li:nth-child(1) a {
    text-decoration: underline;
  }
  &__icon-li:nth-child(1):before {
    content: url("/wp-content/themes/fluidpress/images/telephone-icon.svg");
    width: 22px;
    height: 22px;
    display: inline-block;
    position: absolute;
    left: -2.65rem;
    margin-top: -0.02rem;
  }
  &__icon-li:nth-child(2):before {
    content: url("/wp-content/themes/fluidpress/images/email-icon.svg");
    width: 22px;
    height: 22px;
    display: inline-block;
    position: absolute;
    left: -2.65rem;
    margin-top: -0.02rem;
  }
  &__icon-li:nth-child(3):before {
    content: url("/wp-content/themes/fluidpress/images/address-icon.svg");
    width: 22px;
    height: 22px;
    display: inline-block;
    position: absolute;
    left: -2.65rem;
    margin-top: -0.02rem;
  }
  &__icon-li:nth-child(4):before {
    content: url("/wp-content/themes/fluidpress/images/info-circle-solid.svg");
    width: 22px;
    height: 22px;
    display: inline-block;
    position: absolute;
    left: -2.65rem;
    margin-top: -0.02rem;
  }
  &__tel-icon, 
  &__email-icon, 
  &__address-icon {
    width: 22px;
    height: 22px;
    margin-right: 0.99rem;
  }
  &__signoff {
    background: $black;
    margin-top: 3.4rem;
    font-size: 0.88rem;
    line-height: 1.25rem;
    padding: 1.85rem 0;
    font-weight: $light;
    text-align: center;
    @include iphone {
      text-align: left;
    }
  }
  &__privacy-link {
    margin-top: 1rem;
    display: inline-block;
  }
  &__privacy-icon {
    margin-top: 1rem;
    display: inline-block;
    font-size: 1.2rem;
    margin-right: 1.1rem;
    @include laptop {
      margin-right: 0.5rem;
    }
  }
}
