.privacy {
    &__header {
        background: $primary-color;
        color: $white;
        padding: 2.2rem 0 1.2rem 0;
        @include tablet {
            padding: 1.85rem 0 1.4rem 0;
        }
        @include laptop {
            padding: 4.8rem 0 1.7rem 0;
        }
        @include desktop-small {
            padding: 4.8rem 0 2rem 0;
        }
        @include desktop {
            padding: 4.8rem 0 2.9rem 0;
        }
        margin-bottom: 1rem;
        font-weight: $medium;
    }
    &__content {
        padding: 0.5rem 0 4rem 0;
    }
    &__container {
        padding: 0 0.75rem;
        @include iphone {
            padding: 0;
        }
    }
}