.sector-three-col-gallery {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: -0.2rem;
    &__heading {
        width: 100%;
        text-align: center;
        font-family: $footer-font-family;
        font-size: 1.7rem;
        @include iphone {
            font-size: 2.22rem;
        }
        font-weight: $bold;
    }
    &__col {
        width: 100%;
        // @include iphone {
        //     width: 33.333%;
        // }
    }
    &__col2 {
        width: 100%;
        @include iphone {
            width: 33.333%;
        }
    }
    &__img {
        width: 100%;
        object-fit: cover;
        height: 88vw;
        margin-bottom: -2px;
        @include iphone {
            height: 30vw;
            margin-bottom: 0;
        }
    }
    &__container {
        max-width: 750px;
    }
}