.background-img-panel {
    padding: 4rem 2rem;
    @include iphone {
        padding: 5rem;
    }
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    &::after {
        content: "";
        background: $black;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: 0.35;
    }
    &__container {
        z-index: 9;
        position: relative;
        padding-left: 1.8rem;
        padding-right: 1.8rem;
        padding-top: 2rem;
        padding-bottom: 2rem;
        width: 100%;
        @include iphone {
            padding-left: 2rem;
            padding-right: 2rem;
            padding-top: 4rem;
            padding-bottom: 4rem;
            width: 100%;
        }
        @include tablet {
            padding-left: 2rem;
            padding-right: 2rem;
            padding-top: 4rem;
            padding-bottom: 4rem;
        }
        @include laptop {
            padding-left: 2rem;
            padding-right: 2rem;
            padding-top: 4rem;
            padding-bottom: 4rem;
        }
        @include laptop {
            width: 85%;
        }
        @include desktop {
            width: 1050px;
        }
        margin-left: auto;
        margin-right: auto;
    }
}