.vacancies-intro {
    padding: 4rem 0 0.5rem 0;
    @include tablet {
        padding: 4rem 0 0 0;
    }
    @include laptop {
        padding: 4rem 0 2rem 0;
    }
    &__heading {
        margin-bottom: 1rem;
        text-align: center;
        @include iphone {
            text-align: left;
        }
    }
    &__subheading {
        padding-bottom: 0;
        margin-bottom: 1rem;
        text-align: center;
        @include iphone {
            text-align: left;
        }
    }
    &__content {
        text-align: center;
        @include iphone {
            text-align: left;
        }
    }
}

.current-vacancies {
    background: $light-grey-background;
    padding: 1.6rem 2rem 0 2rem;
    @include iphone {
        padding: 3.5rem 2rem 2rem 2rem;
    }
    &__panel {
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
    }
    &__heading {
        font-size: 1.49rem;
        @include iphone {
            font-size: 2.28rem;
        }
        font-weight: $bold;
    }
    &__subheading {
        font-weight: $light;
        font-size: 1.15rem;
        line-height: 1.8rem;
        @include iphone {
            font-size: 1.45rem;
            line-height: 1.8rem;
        }
        margin-bottom: 1.5rem;
        margin-top: 0;
    }
    &__list {
        background: $darker-background;
        color: $white;
        font-size: 1.05rem;
        margin: 0.55rem 0;
        @include iphone {
            font-size: 1.55rem;
            margin: 1.65rem 0;
        }
        padding: 0.75rem;
    }
    &__nav {
        display: flex;
        margin-top: 0.5rem;
    }
    &__btn {
        width: 50%;
        text-transform: unset;
        font-size: 1.05rem;
        padding: 1.2rem;
        @include iphone {
            font-size: 1.43rem;
            padding: 1.45rem;
        }
    }
    &__btn:nth-child(1) {
        margin-right: 1.5%;
    }
    &__btn:nth-child(2) {
        margin-left: 1.5%;
    }
}

.vacancies-signoff {
    padding: 2rem;
    &__content {
        max-width: 770px;
        margin-left: auto;
        margin-right: auto;
    }
}

.vacancies-modal {
    &__close {
        cursor: pointer;
        padding: 1rem;
        margin: -1rem;
        color: $white;
    }
    &__inner {
        background: $primary-color;
        max-width: 500px;
    }
    &__heading {
        color: $white;
        padding: 0;
        margin: 0;
        font-family: $footer-font-family;
        margin-bottom: -1.7rem;
        margin-top: -0.9rem;
    }
    &__textarea {
        height: 7rem;
        margin: 0;
        min-height: 100px;
        // margin-top: 0.1rem;
        @include iphone {
            min-height: auto;
            margin-top: 0;
        }
        
    }
    &__upload {
        color: $white;
        font-size: 0.75rem;
    }
    &__upload-btn {
        color: transparent;
        width: 190px;
        max-width: 190px;
        padding: 0.4rem 0 1.3rem 3.7rem;
        position: relative;
        @include iphone {
            padding: 0.4rem 0 2.7rem 3.7rem;
        }
        box-shadow: none;
    }
    &__upload-btn::-webkit-file-upload-button {
        visibility: hidden;
        @include tablet {
            visibility: visible;
        }
    }
    &__upload-btn::before {
        content: 'Upload your file';
        color: $primary-color;
        display: inline-block;
        background: $white;
        border: none;
        padding: 5px 0;
        outline: none;
        white-space: nowrap;
        -webkit-user-select: none;
        cursor: pointer;
        font-weight: $bold;
        text-transform: uppercase;
        letter-spacing: 0.02rem;
        font-size: 10pt;
        width: 190px;
        text-align: center;
        position: absolute;
        left: 0;
    }
    &__upload-btn:active {
        outline: none;
    }
    &__upload-btn:hover::before {
        border-color: $pale-grey-background;
    }
    &__upload-btn:active::before {
        background-color: $pale-grey-background; 
    }
    &__upload-name {
        width: 100%;
        margin-top: 0;
        @include iphone {
            margin-top: -1.4rem;
        }
        font-size: 0.8rem;
        padding-bottom: 0.75rem;
        color: $white;
    }
}