// // Header.
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 0.5rem 0;
  background: $white;
  box-shadow: 0px 0px 7px rgba(0, 0, 0, 0.2);
  bottom: unset;
  @include iphone {
    padding: 0.6rem 0;
    text-align: center;
  }
  @include laptop {
    padding: 0.65rem 1.5em 0.65rem 0.8rem;
    text-align: left;
  }
  &--active {
    position: fixed;
    padding: 0.45rem 0 0.45rem 0;
    @include laptop {
      padding: 0.45rem 1.5em 0.45rem 0.8rem;
    }
  }
  &__layout {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @include laptop {
      flex-wrap: nowrap;
    }
  }
  &__logo {
    width: 4.5rem;
    margin-left: 1.5rem;
    margin-bottom: 0;
    margin-top: 0.15rem;
    @include iphone {
      width: 5.5rem;
      margin-left: 1.5rem;
      margin-bottom: 0;
      margin-top: 0;
    }
    @include tablet {
      width: 5.5rem;
      margin-left: 1.7rem;
      margin-bottom: 0;
    }
    @include laptop {
      width: 8rem;
      margin-left: 0;
    }
    @include desktop-small {
      width: 8rem;
      margin-left: 0;
    }
    @include desktop {
      width: 8rem;
      margin-left: 0;
    }
  }
  &__contact {
    background: $primary-color;
    padding: 0.9rem 1rem;
    color: $white;
    text-transform: uppercase;
    letter-spacing: 0;
    font-size: 0.88rem;
    margin-top: 1.9rem;
    margin-bottom: 1rem;
    width: 180px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    transition: background 0.15s ease-in;
    @include iphone {
      font-size: 0.95rem;
      padding: 1.1rem 1.3rem;
    }
    @include laptop {
      width: auto;
      padding: 1.1rem 1.3rem;
      letter-spacing: 0.15rem;
      margin-top: 0;
      margin-bottom: 0.4rem;
      margin-left: 1rem;
    }
  }
  &__contact:hover {
    background: darken($primary-color, 15%);
  }
  &__link {
    color: $black;
    padding: 0.7rem 0rem;
    display: block;
    @include iphone {
      padding: 1rem 0rem;
    }
    @include laptop {
      padding: 0 1rem;
      display: inline;
    }
    @include desktop-small {
      padding: 0 1.9rem;
      display: inline;
    }
  }
  &__link-email {
    margin-top: 0.48rem;
    @include iphone {
      margin-top: 0.88rem;
    }
  }
  &__link--active {
    display: block;
    @include laptop {
      display: none;
    }
  }
  &--loggedin {
    margin-top: 32px;
    // margin-top: 47px;
    // @include tablet {
    //   margin-top: 32px;
    // }
  }
}



// Navigation.
.navigation {
  display: none;
  flex-basis: 100%;
  padding: 1vh 2.2rem 1vh 2.2rem;
  text-align: left;
  background: linear-gradient(to bottom,  rgba(238,238,238,1) 0%,rgba(255,255,255,1) 1.4%); 
  margin-top: 0.6rem;
  font-size: 0.95rem;
  @include iphone {
    font-size: 1rem;
  }
  @include laptop {
    margin-top: 0;
    padding: 1.65rem 0 0 0;
    flex-basis: unset;
    display: initial;
    text-align: right;
    background: transparent;
  }
  &--open {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 78vh;
    text-align: center;
    @include laptop {
      height: auto;
      text-align: right;
    }
    flex-direction: column;
    position: relative;
    animation-name: menu-animation-2;
    animation-duration: .9s;
    animation-fill-mode: forwards;
  }
  @keyframes menu-animation-2 {
    0%   {opacity: 0;}
    100% {opacity: 1;}
  }
  .menu-item-has-children {
    position: relative;
    &:hover {
      .menu-list {
        display: block;
      }
    }
  }
  .menu-item-has-children > a {
    position: relative;
    padding-right: 2rem !important; // sass-lint:disable-line no-important
    &:after {
      @extend %pseudo;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background: url("../../dist/assets/images/icons/chevron-down.svg")
        no-repeat center;
      right: 5px;
      width: 15px;
      height: 15px;
      z-index: 2;
    }
  }
  .menu {
    flex-wrap: wrap;
    width: 100%;
    margin-top: 0.5rem;
    font-size: 1.05rem;
    @include iphone {
      font-size: 1.3rem;
    }
    @include laptop {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 0.4rem;
      margin-right: 0.1rem;
      margin-top: -1.3rem;
      flex-wrap: nowrap;
      width: auto;
      font-size: 1rem;
    }

    ul {
      display: none;
      background: $dark-background;
      padding: 1rem 1.5rem;
      @include tablet {
        position: absolute;
        top: 100%;
        left: 0;
      }
    }
    li {
      padding: 0rem 0 0.8vh 0;
      @include iphone {
        margin: 0 1.4rem 0 0;
        padding: 0rem 0 0.6rem 0;
        display: block;
        width: 100%;
      }
      @include tablet {
        margin: 0 1.4rem 0 0;
        padding: 0.5rem 0 0.6rem 0;
        display: block;
        width: 100%;
      }
      @include laptop {
        margin: 0 1.4rem 0 0;
        padding: 0.5rem 0 0.6rem 0;
        display: inline;
        width: auto;
      }
      @include desktop {
        margin: 0 2.5rem 0 0;
      }
      &:first-of-type {
        margin-left: unset;
      }
      &:last-of-type {
        margin-right: unset;
      }
      a {
        @extend %hover;
        font-weight: $medium;
        color: $black;
        text-transform: uppercase;
        &--active {
          font-weight: $bold;
        }
      }
    }
  }
}
.header--active .header__logo {
  width: 5.5rem;
  margin-top: 2.5px;
}