.carousel {
    width: 100%;
    height: auto;
    position: relative;
    margin-top: -5px;
    @include tablet {
        margin-top: -12px;
    }
    @include laptop {
        margin-top: -5px;
    }
    @include desktop-small {
        margin-top: 0;
    }
    &__card {
        background: rgba(41, 38, 99, 0.76);
        z-index: 9;
        padding: 1rem 2.3rem 1.3rem 2.3rem;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 200px;
        margin-left: -100px;
        margin-top: -135px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        @include iphone {
            padding: 1rem 2rem 1.2rem 2rem;
            top: 3.6rem;
            bottom: 5.6rem;
            left: 50%;
            margin-top: 0;
            width: 168px;
            margin-left: -84px;
        }
        @include tablet {
            top: 5rem;
            bottom: 7rem;
            left: 50%;
            width: 230px;
            margin-left: -115px;
        }
        @include laptop {
            padding: 1.8rem 3.5rem 2rem 3.5rem;
            top: 9rem;
            bottom: 9rem;
            left: 50%;
            width: 330px;
            margin-left: -165px;
        }
        @include desktop-small {
            padding: 2rem 5.5rem;
            top: 10rem;
            left: 12vw;
            bottom: 10rem;
            width: 440px;
            margin-left: 0;
        }
        @include desktop {
            padding: 2rem 5.5rem;
            top: 12rem;
            left: 12vw;
            bottom: 12rem;
            width: 440px;
            margin-left: 0;
        }
    }
    &__card-small {
        background: rgba(41, 38, 99, 0.76);
        z-index: 9;
        padding: 1rem 2.3rem 1.3rem 2.3rem;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 180px;
        margin-left: -90px;
        margin-top: -125px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        @include iphone {
            padding: 1rem 2rem 0.6rem 2rem;
            top: 4.6vw;
            bottom: 4.6vw;
            left: 2.3rem;
            width: auto;
            margin-left: 0;
            margin-top: 0;
        }
        @include tablet {
            padding: 1.8rem 3.5rem 0.6rem 3.5rem;
            top: 6vw;
            bottom: 6vw;
            left: 10vw;
        }
        @include laptop {
            padding: 1.8rem 3.5rem 0.9rem 3.5rem;
            top: 6.5vw;
            bottom: 5.5vw;
            left: 12vw;
        }
        @include desktop {
            padding: 2rem 5.5rem 0.9rem 5.5rem;
            top: 6vw;
            left: 12vw;
            bottom: 6vw;
        }
    }
    &__heading:nth-child(1) {
        top: 0;
    }
    &__heading:nth-child(3) {
        bottom: 0;
        @include tablet {
            bottom: 0.2rem;
        }
        @include laptop {
            bottom: 0.5rem;
        }
    }
    &__heading {
        color: $white;
        font-size: 1.55rem;
        line-height: 14.8vw;
        padding: 0;
        position: relative;
        @include iphone {
            position: absolute;
            font-size: 1.4rem;
            line-height: 4rem;
            width: 100%;
            left: 0;
            right: 0;
            text-align: center;
        }
        @include tablet {
            font-size: 1.8rem;
            line-height: 7rem;
            text-align: center;
        }
        @include laptop {
            font-size: 2rem;
            line-height: 7.5rem;
            left: 0;
            right: 0;
            text-align: center;
        }
        @include desktop-small {
            font-size: 3rem;
            line-height: 8rem;
            left: 2.5rem;
            right: 0;
            text-align: left;
        }
        @include desktop {
            font-size: 3.5rem;
            line-height: 9.5rem;
        }
        font-weight: $medium;
    }
    &__img {
        width: 100%;
        object-fit: cover;
        max-height: 800px;
        height: 83vh;
        @include iphone {
            height: 310px;
        }
        @include tablet {
            height: 430px;
        }
        @include laptop {
            height: 550px;
        }
        @include desktop-small {
            height: 650px;
        }
        @include desktop {
            height: 700px;
        }
    }
}