.blog-carousel {
    padding: 2.3rem 0.9rem 2.95rem 0.9rem;
    @include iphone {
        padding: 4rem 1rem;
    }
    @include tablet {
        padding: 4rem 0;
    }
    &__main {
        height: auto;
        // @include iphone {
        //     height: auto;
        // }
        // @include tablet {
        //     height: 500px;
        // }
        // @include laptop {
        //     height: 450px;
        // }
    }
    &__card {
        background: $primary-color;
        color: $white;
        padding: 1.3rem 1.3rem 1.6rem 1.3rem;
        margin: 1.35rem 0rem 0rem 0rem;
        @include iphone {
            padding: 2.6rem 4rem 4rem 4rem;
            margin: 3rem 0.5rem 0rem 0.5rem;
        }
    }
    &__card-title {
        color: $white;
        font-family: $footer-font-family;
        font-size: 1.25rem;
        line-height: 1.5rem;
        padding-bottom: 1.15rem;
        @include iphone {
            font-size: 2rem;
            line-height: 2.5rem;
            padding-bottom: 1.1rem;
        }
        @include desktop-small {
            font-size: 1.8rem;
            line-height: 2.3rem;
            padding-bottom: 1.1rem;
        }
        @include desktop {
            font-size: 2rem;
            line-height: 2.5rem;
            padding-bottom: 1.1rem;
        }
    }
    &__card-btn {
        color: $white;
        margin-top: 2rem;
        @include iphone {
            margin-top: 3rem;
        }
    }
}