.about-intro {
    background: $pale-grey-background;
    padding: 2.2rem 0 0.05rem 0;
    // margin-bottom: 3rem;
    &__img-wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
        @include iphone {
            margin-left: 0;
            margin-right: auto;
            margin-top: 1.1rem;
            margin-bottom: 0.3rem;
        }
    }
    &__img {
        width: 40%;
        @include iphone {
            width: 21%;
        }
        margin-bottom: 1rem;
    }
    &__heading {
        text-align: center;
        @include iphone {
            text-align: left;
            font-size: 2.28rem;
            line-height: 3.35rem;
            margin-bottom: 1rem;
        }
        @include tablet {
            font-size: 2rem;
            line-height: 2.8rem;
            margin-bottom: 1rem;
        }
        @include laptop {
            font-size: 2.28rem;
            line-height: 3.35rem;
        }
    }
    &__subheading {
        font-size: 1.25rem;
        letter-spacing: 0.01rem;
        line-height: 1.8sssrem;
        padding-bottom: 0.2rem;
        padding-right: 2rem;
        text-align: left;
        @include iphone {
            font-size: 1.55rem;
            line-height: 2.19rem;
        }
    }
}