.grid-layout-4col {
    grid-template-columns: repeat(1, 1fr);
    @include tablet {
        padding: 2rem 0;
        grid-template-columns: repeat(2, 1fr);
    }
    @include laptop {
        padding: 2rem 0;
        grid-template-columns: repeat(3, 1fr);
    }
    @include desktop {
        padding: 2rem 0;
        grid-template-columns: repeat(4, 1fr);
    }
}

.grid-layout-4 {
    padding: 0;
    margin-bottom: 0;
    @include tablet {
        margin-bottom: 4rem;
    }
    display: grid;
    grid-row-gap: 1.65rem;
    grid-column-gap: 1.2rem;
    &__col {
        width: 100%;
        height: auto;
        margin-bottom: 0;
        padding: 1.5rem;
    }

}