.supply-chain-clients {
    padding: 3rem 0;
    &__card {
        box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.3);
        width: 98%;
        margin-left: 1%;
        margin-right: 1%;
        margin-top: 0.2rem;
        margin-bottom: 0.2rem;
        @include iphone {
            margin-left: 1%;
            margin-right: 1%;
            margin-top: 0.2rem;
            margin-bottom: 0.2rem;
        }
        @include tablet {
            margin-left: 0;
            margin-right: 0;
            margin-top: 0;
            margin-bottom: 0;
        }
    }
    &__heading {
        margin-bottom: 2rem;
    }
    &__card-title {
        font-size: 1.05rem;
        line-height: 1.4rem;
        min-height: 52px;
        text-align: center;
        color: $primary-color;
        padding: 0;
        margin: 0;
    }
    &__card-subtitle {
        font-size: 1rem;
        line-height: 1.3rem;
        text-align: center;
        color: #c4bcd7;
        padding: 0 0 0.3rem 0;
        margin: 0 0 0 0;
        min-height: 45px;
    }
    &__card-content {
        font-size: 0.95rem;
        line-height: 1.5rem;
        //font-weight: $light;
    }
    &__card-link {
        font-weight: $bold;
        padding-left: 0;
        text-decoration: underline;
        color: $black;
        transition: color 0.1s ease-in;
    }
    &__card-link:hover {
        color: $primary-color;
    }
    &__desktop {
        visibility: hidden;
        display: none;
        @include tablet {
            display: grid;
            visibility: visible;
        }
    }
    &__mobile {
        visibility: visible;
        display: block;
        margin: 0 0.7rem 0 0.7rem;
        @include tablet {
            visibility: hidden;
            display: none;
            margin: 0;
        }
    }
}

.modal {
    position: fixed;
    background: rgba(0,0,0,0.6);
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    display: none;
    z-index: 99;
    &--active {
        display: block;
        z-index: 99;
    }
    &__inner {
        position: fixed;
        width: 90%;
        max-width: 670px;
        min-height: 250px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: $white;
        padding: 1rem;
        @include iphone {
            padding: 1.5rem;
        }
    }
    &__close {
        cursor: pointer;
        padding: 1rem;
        margin: -1rem;
    }
    &__spinner {
        border: 6px solid $primary-color; 
        border-top: 6px solid lighten($primary-color, 80%); 
        border-radius: 50%;
        width: 52px;
        height: 52px;
        animation: spin 2s linear infinite;
        position: absolute;
        left: 50%;
        top: 45%;
        margin-left: -26px;
        opacity: 0.8;
    }
    &__content.hidden {
        display: none;
    }
      
    @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
    }
    &__spinner.hidden {
        display: none;
    }
    &__loading.hidden {
        display: none;
    }
  }