// Toggle.
.toggle {
  $self: &;
  width: 38px;
  height: 24px;
  // width: 40px;
  // height: 30px;
  right: 1.5rem;
  @include laptop {
    right: 0;
  }
  background: transparent;
  position: relative;
  padding: initial;
  cursor: pointer;
  border: unset;
  transition: all 0.35s ease-in-out;
  z-index: 4;
  outline: none;
  color: $black;
  @include laptop {
    display: none;
  }

  &:before,
  &:after,
  &__line {
    @extend %pseudo;
    position: absolute;
    width: 100%;
    height: 3px;
    background: $black;
    transition: all 0.25s ease-in-out;
  }
  &:before {
    top: 0;
  }
  &:after {
    top: 100%;
    width: 100%;
  }
  &__line {
    top: 54%;
    transform: translateY(-50%);
  }
  &--open {
    &:before {
      transform: rotate(-45deg) translate(-6px, 6px);
      //transform: rotate(-45deg) translate(-10px, 10px);
    }
    #{$self}__line {
      opacity: 0;
    }
    &:after {
      transform: rotate(45deg) translate(-10px, -11px);
      width: 100%;
    }
  }
}
