.sectors-menu {
    background: $primary-color;
    padding: 3.2rem 0 3.9rem 0;
    @include iphone {
        padding: 5rem 0 2.8rem 0;
    }
    &__container {
        padding: 0;
        @include desktop-small {
            padding: 0;
        }
        @include desktop {
            padding: 0 4rem;
        }
    }
    &__navigation {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &__btn {
        width: 100%;
        margin: 0 1.2% 0.45rem 1.2%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1.25rem 2.5rem;
        font-size: 1rem;
        background: transparent;
        height: auto;
        @include iphone {
            width: 30.6%;
            // padding: 1.5rem 2.5rem;
            margin: 0 1.2% 1.2rem 1.2%;
            padding: 1.25rem 1rem;
            font-size: 1rem;
            height: 77px;
        }
        @include tablet {
            width: 22.6%;
            font-size: 1rem;
            padding: 1.35rem 1rem;
            margin: 0 1.2% 1.3rem 1.2%;
            height: 77px;
        }
        @include desktop-small {
            width: 22.6%;
            font-size: 1.1rem;
            padding: 1.35rem 1rem;
            margin: 0 1.2% 1.3rem 1.2%;
            height: 77px;
        }
        @include desktop {
            font-size: 1.2rem;
            padding: 1.85rem 1rem;
            margin: 0 1.2% 2.1rem 1.2%;
            height: auto;
        }
    }
    &__btn:hover {
        background: $white;
        color: $primary-color;
    }
}