.two-column {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    // margin-top: 3.3rem;
    padding: 0 0.9rem;
    @include iphone {
        padding: 0;
    }
    &__col:first-of-type {
        width: 100%;
        margin-top: 0.6rem;
        @include iphone {
            margin-top: 2.6rem;
        }
        @include tablet {
            width: 100%;
            margin-top: 2.6rem;
        }
        @include laptop {
            width: 35.5%;
        }
        @include desktop {
            width: 48%;
        }
    }
    &__col:last-child {
        width: 100%;
        margin-top: 2.6rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @include tablet {
            width: 100%;
            margin-top: 2.6rem;
        }
        @include laptop {
            width: 60.5%;
        }
        @include desktop {
            width: 48%;
        }
    }
    &__col-2:first-of-type {
        width: 100%;
        @include tablet {
            width: 55%;
        }
    }
    &__col-2:last-child {
        width: 100%;
        margin-left: 0%;
        @include tablet {
            width: 41%;
            margin-left: 4%;
        }
    }
    &__content {
        margin: 0 0 1.5rem 0;
        padding: 0;
    }
    &__img {
        object-fit: cover;
        height: 227px;
        width: 100%;
        @include iphone {
            height: 277px;
        }
        @include laptop {
            height: 290px;
        }
        @include desktop {
            height: 452px;
        }
    }
    &__btn {
        width: 160px;
        margin-top: -1rem;
        margin-left: auto;
        margin-right: auto;
        padding: 1rem 1.5rem;
        letter-spacing: 0.02rem;
        @include iphone {
            width: 170px;
            margin-top: 2rem;
            margin-left: 0;
            margin-right: 0;
        }
        margin-bottom: 0.4rem;
    }
}