.clients {
    background-image: url('/wp-content/themes/fluidpress/images/clients-backgound.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    padding: 2.25rem;
    @include iphone {
        padding: 5rem;
    }
    &::after {
        content: "x";
        background: $black;
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        opacity: 0.35;
    }
    &__wrapper {
        width: 100%;
        height: auto;
        background: $white;
        text-align: center;
    }
    &__heading {
        color: $primary-color;
        font-size: 1.49rem;
        @include iphone {
            font-size: 2rem;
        }
        font-weight: $semi-bold;
    }
    &__content {
        font-size: 1rem;
        line-height: 1.5rem;
        margin: 1.8rem 0 2rem 0;
        display: block;
        font-weight: $light;
        @include iphone {
            margin: 1.8rem 2rem 2rem 2rem;
            font-size: 1.2rem;
            line-height: 2.2rem;
        }
    }
    &__name {
        color: $primary-color;
        padding: 0;
        margin-top: 1.5rem;
    }
    &__title {
        color: $primary-color;
        padding: 0;
        margin-bottom: 2rem;
    }
}