.projects {
    background: $primary-color;
    padding: 3.2rem 2.2rem 2rem 2.2rem;
    @include iphone {
        padding: 3.2rem 3.7rem 2rem 3.7rem;
    }
    color: $white;
    position: relative;
    &--single {
        padding: 3.2rem 3.7rem 2rem 3.7rem;
        @include iphone {
            padding: 3.2rem 0 2rem 0;
        }
    }
    &__navigation {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 1.7rem;
        width: 100%;
        @include tablet {
            width: 100%;
            margin-top: 2.5rem;
        }
        @include laptop {
            width: 85%;
            margin-top: 2.8rem;
        }
        @include desktop-small {
            width: 70%;
            margin-top: 2.8rem;
        }
        @include desktop {
            width: 70%;
            margin-top: 3rem;
        }
        margin-left: auto;
        margin-right: auto;
    }
    &__sector-navigation {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 0;
        margin-bottom: 4rem;
        width: 100%;
        // @include tablet {
        //     width: 100%;
        // }
        // @include laptop {
        //     width: 98%;
        // }
        // @include desktop {
        //     width: 98%;
        // }
        margin-left: auto;
        margin-right: auto;
    }
    &__btn {
        width: 100%;
        margin: 0 0 0.5rem 0;
        background: transparent;
        height: auto;
        @include iphone {
            width: 22.9%;
            margin: 0 1.05% 1.3rem 1.05%;
            height: 67px;
        }
        @include tablet {
            height: 67px;
        }
        @include laptop {
            height: auto;
        }
        padding: 1.18rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__btn:hover {
        background: $white;
        color: $primary-color;
    }
    &__sector-btn {
        width: 100%;
        margin: 0.3rem 0.45% 0.3rem 0.45%;
        padding: 1rem;
        @include iphone {
            width: 32%;
            margin: 0.3rem 0.45% 0.3rem 0.45%;
        }
        @include tablet {
            width: 24.1%;
            margin: 0.3rem 0.45% 0.3rem 0.45%;
        }
        @include desktop {
            width: 13.385%;
            margin: -0.9rem 0.45% -1.3rem 0.45%;
        }
        
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__sector-btn:hover {
        background: $white;
        color: $primary-color;
        font-weight: $bold;
    }
    &__sector-btn:first-of-type {
        margin-left: 0.45%;
        @include desktop {
            margin-left: 0;
        }
    }
    &__sector-btn:last-of-type {
        margin-right: 0.45%;
        @include desktop {
            margin-right: 0;
        }
    }
    &__sector-btn.active {
        background: white;
        color: $primary-color;
    }
    // &__carousel {
    //     margin: 2.7rem 0 2rem 0;
    //     @include iphone {
    //         margin: 4.5rem 0 2rem 0;
    //     }
    // }
    &__wrapper {
        min-height: 380px;
        position: relative;
        margin: 2.7rem 0 2rem 0;
        @include iphone {
            margin: 4.5rem 0 2rem 0;
        }
    }
    &__spinner {
        border: 6px solid $white; 
        border-top: 6px solid transparent; 
        border-radius: 50%;
        width: 52px;
        height: 52px;
        animation: spin 2s linear infinite;
        position: absolute;
        left: 50%;
        top: 45%;
        margin-left: -26px;
        opacity: 0.8;
    }
    &__content.hidden {
        display: none;
    }
      
    @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
    }
    &__spinner.hidden {
        display: none;
    }
    &__card {
        background: transparent;
        position: relative;
    }
    &__card-no-img {
        min-height: 370px;
        border: 2px solid $white;
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    &__card-img {
        object-fit: cover;
        height: 380px;
        border: 0.5px solid $primary-color;
        @include desktop {
            height: 420px;
        }
    }
    &__card-action {
        background: rgba(30, 30, 30, 0.8);
        width: 100%;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        padding: 0.5rem 14vw 2.5rem 14vw;
        transition: all 0.1s ease-in;
        @include iphone {
            padding: 0.5rem 2vw 2.5rem 2vw;
        }
        @include tablet {
            padding: 0.5rem 2vw 2.5rem 2vw;
        }
    }
    &__card-action:hover {
        padding: 0.5rem 14vw 3.1rem 14vw;
        @include iphone {
            padding: 0.5rem 2vw 3.1rem 2vw;
        }
        background: rgba(30, 30, 30, 1);
    }
    &__card-description {
        font-size: 0.8rem;
        line-height: 1.1rem;
        text-align: center;
        margin-bottom: 1rem;
        color: $white;
        min-height: 67px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    &__card-btn {
        padding: 1rem 0.5rem;
        @include iphone {
            padding: 1rem;
        }
        background-color: $white;
        color: $black;
        font-size: 0.8rem;
        letter-spacing: 0.01rem;
        width: 100%;
    }
    &__card-btn:hover {
        background: #dedede;
    }
}