.mpb-heading {
    font-weight: $semi-bold;
    font-size: 1.49rem;
    line-height: 2rem;
    color: $black;
    @include iphone {
        font-size: 2.28rem;
        line-height: 3.5rem;
    }
    width: 100%;
    padding-bottom: 0.4rem;
    padding-top: 0;
    &--black {
        color: $black;
    }
    &--purple {
        color: $primary-color;
    }
    &--white {
        color: $white;
    }
    &--font-alt {
        font-family: $footer-font-family;
    }
}

.mpb-heading-small {
    font-weight: $semi-bold;
    font-size: 1.49rem;
    line-height: 2.6rem;
    color: $black;
    @include iphone {
        font-size: 2.1rem;
        line-height: 3.35rem;
    }
    width: 100%;
    padding-bottom: 0.4rem;
    &--black {
        color: $black;
    }
    &--purple {
        color: $primary-color;
    }
    &--white {
        color: $white;
    }
}

.mpb-subheading {
    width: 100%;
    font-size: 1.15rem;
    line-height: 1.6rem;
    padding: 0;
    @include iphone {
        font-size: 1.35rem;
        line-height: 1.8rem;
        padding: 0;
    }
    color: $primary-color;
    font-weight: $normal;
    padding-bottom: 1.5rem;
}

.text-center {
        text-align: center;
}