.page-not-found {
    padding: 4rem 0;
    &__subheading {
        background: $primary-color;
        color: $white;
        text-align: center;
        text-transform: uppercase;
        font-size: 1rem;
        line-height: 1.3rem;
        @include iphone {
            font-size: 1.2rem;
            line-height: 1.5rem;
        }
        letter-spacing: 0.02rem;
        padding: 0.9rem 1.9rem;
    }
    &__content {
        font-weight: $bold;
        margin-bottom: 0.7rem;
        padding: 0;
    }
}