body {
  margin-top: 4.7rem;
  @include iphone {
    margin-top: 6rem;
  }
  @include tablet {
    margin-top: 6rem;
  }
  @include laptop {
    margin-top: 6rem;
  }
  @include desktop {
    margin-top: 7.2rem;
  }
}
.body--fixed {
  position: fixed;
  width: 100%;
  margin-top: 0;
  top: -100%;
  @include iphone {
    position: relative;
    margin-top: 6rem;
    top: 0;
  }
  @include tablet {
    margin-top: 6rem;
  }
  @include laptop {
    margin-top: 6rem;
  }
  @include desktop {
    margin-top: 7.2rem;
  }
}
.body--menu-fixed {
  position: fixed;
  width: 100%;
  margin-top: 0;
}

// Header
  .header__logo--active {
    width: 4.5rem;
  }
  .header--toggle-active .header__logo--active {
    width: 4.5rem !important;
    @include iphone {
      width: 5.5rem !important;
    }
  }
  // .menu--active {
  //   margin-top: -0.6rem !important;
  // }
  .navigation {
    display: none;
    @include laptop {
      display: block !important;
    }
  }
  .header--toggle-active {
    position: fixed !important;
    bottom: 0;
    @include laptop {
      bottom: unset;
    }
  }
  .navigation--open {
    display: flex !important;
    @include laptop {
      display: unset !important;
    }
  }
  @media only screen and (max-width: 782px) {
    .header--loggedin {
      margin-top: 47px;
    }
  }
// Header

// Admin Bar
#wpadminbar {
  position: fixed !important;
}

// Owl Carousel 
.owl-nav .owl-prev {
