}
.owl-carousel .owl-dots {
  position: absolute;
  bottom: 1.6rem;
  @include iphone {
    bottom: 2.6rem;
  }
  left: 50%;
  margin-left: -100px;
  width: 200px;
  text-align: center;
}
.owl-carousel .owl-dots .owl-dot {
  width: 13px;
  height: 13px;
  @include iphone {
    width: 16px;
    height: 16px;
  }
  @include tablet {
    width: 16px;
    height: 16px;
  }
  @include laptop {
    width: 16px;
    height: 16px;
  }
  @include desktop {
    width: 20px;
    height: 20px;
  }
  background: $white;
  border-radius: 50%;
  opacity: .5;
  margin: 0 0.25rem;
}
.owl-carousel .owl-dots .active {
  opacity: 1;
}
