// Blog Carousel 
.blog-carousel__card p {
  font-weight: $light;
  line-height: 1.39rem;
  font-size: 0.9rem;
  min-height: auto;
  @include iphone {
    line-height: 1.65rem;
    font-size: 1rem;
    min-height: auto;
  }
  @include laptop {
    line-height: 1.65rem;
    font-size: 1rem;
    min-height: 100px;
  }
  @include desktop-small {
    line-height: 1.55rem;
    font-size: 0.85rem;
    min-height: 160px;
  }
  @include desktop {
    line-height: 1.65rem;
    font-size: 1rem;
    min-height: 150px;
  }
}
