// Supply Chain Testimonials
.supply-chain-clients__card-content p {
  line-height: 1.6rem;
  padding: 0.5rem 0;
}
.supply-chain-clients__card-content p::before {
  content: '"';
}
.supply-chain-clients__card-content p::after {
  content: '"';
}
.supply-chain-clients__card-no-quote p::after {
  content: "";
}
