// Pagination
.pagination {
  padding: 0 0 2rem 0;
}
.woocommerce nav.woocommerce-pagination ul li a, .current {
  background: #c6c6c6 !important;
}
.woocommerce nav.woocommerce-pagination ul li a, .page-numbers {
  padding: 0;
  margin: 0 0.2rem;
  background: #fff;
  border: 2px solid #c6c6c6;
  transition: background 0.06s ease-in;
  border-radius: 40px;
  height: 30px;
  width: 30px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.woocommerce nav.woocommerce-pagination ul li a, .next {
  padding: 0;
  background: #fff;
  border: 0px solid #c6c6c6;
  transition: background 0.06s ease-in;
  border-radius: 40px;
  height: auto;
  width: auto;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.woocommerce nav.woocommerce-pagination ul li a, .prev {
  padding: 0;
  background: #fff;
  border: 0px solid #c6c6c6;
  transition: background 0.06s ease-in;
  border-radius: 40px;
  height: auto;
  width: auto;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
// Pagination

// News Detail Page
.news-detail {
  & h1, & h2, & h3, & h4, & h5, & h6 {
    padding: 0 0 0.8rem 0;
    line-height: 2rem;
  }
  & p {
    padding: 0 0 1.6rem 0;
  }
  & ul {
    list-style: disc;
    margin-left: 1rem;
    margin-bottom: 1.7rem;
  }
