// Generic Page
.page {
  padding: 3rem 0;
  &__title {
    font-weight: $medium;
    font-size: 1.5rem;
    text-align: center;
    @include iphone {
      font-size: 2.28rem;
      text-align: left;
    }
    padding: 0 0 0.8rem 0;
  }
  & h2 {
    font-weight: $bold;
    padding: 0;
  }
  & h3 {
    font-weight: $bold;
    padding: 0 0 0.6rem 0;
  }
  & h4 {
    font-weight: $bold;
    padding: 0 0 0.6rem 0;
  }
  & ul {
    list-style-type: disc;
    margin: 0 0 1rem 1.2rem;
  }
}
