// Vacacies Modal
.modal {
  & .contact__input {
    width: 100%;
    padding: 0.2rem 0.6rem;
    color: $black;
    background: $white;
    font-size: 0.8rem;
    border-radius: 0;
    margin: 0 0 0.25rem 0;
    @include iphone {
        font-size: 0.9rem;
        padding: 0.89rem 1.2rem;
        margin: 0 0 0.99rem 0;
    }
  }
  & .contact__submit {
    width: 190px;
    padding: 0.29rem 1.2rem;
    font-weight: $normal;
    -webkit-appearance: none;
    display: block;
    @include iphone {
        margin-top: 0;
        padding: 0.89rem 1.2rem;
        font-weight: $light;
    }
    color: $primary-color;
    background: $white;
    font-size: 0.9rem;
    letter-spacing: 0.01rem;
    text-transform: uppercase;
    border-radius: 0;
    cursor: pointer;
}
}
.vacancies-modal__textarea {
