.feature-heading-light {
    background: $primary-color;
    padding: 0.9rem 1rem;
    @include desktop {
        padding: 0.9rem 4rem;
    }
    &__container {
        display: flex;
        flex-wrap: wrap;
    }
    &__title {
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 1.49rem;
        width: 100%;
        height: 85px;
        @include iphone {
            font-size: 1.8rem;
        }
        @include tablet {
            font-size: 1.4rem;
            line-height: 1.7rem;
            width: 24.5%;
            height: 130px;
        }
        @include laptop {
            font-size: 1.5rem;
            width: 24.5%;
            height: 130px;
        }
        @include desktop {
            font-size: 2.15rem;
        }
        color: $white;
        margin: 2.1rem 0 2.1rem 0;
        padding-left: 4.5%;
        padding-bottom: 0.9rem;
        border-left: 10px solid $white;
    }
    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        color: $white;
        padding: 0.5rem 0 2.5rem 0;
        @include tablet {
            width: 75.5%;
            padding: 2.5rem 0 2.5rem 3rem;
        }
        font-size: 1.15rem;
        line-height: 1.8rem;
        font-weight: $light;

    }
    
}

.feature-heading-dark {
    background: $tertiary-color;
    padding: 0.9rem 1rem;
    @include desktop {
        padding: 0.9rem 4rem;
    }
    &__container {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row-reverse;
    }
    &__title {
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 1.49rem;
        text-align: right;
        width: 100%;
        height: 85px;
        border-right: 10px solid $white;
        border-left: none;
        padding-right: 4.5%;
        padding-left: 0;
        @include iphone {
            font-size: 1.8rem;
        }
        @include tablet {
            padding-left: 4.5%;
            padding-right: 0;
            font-size: 1.4rem;
            line-height: 1.7rem;
            width: 14.5%;
            height: 130px;
            border-left: 10px solid $white;
            border-right: none;
        }
        @include laptop {
            font-size: 1.5rem;
            width: 14.5%;
            height: 130px;
        }
        @include desktop {
            font-size: 2.15rem;
        }
        color: $white;
        margin: 2.1rem 0 2.1rem 0;
        padding-bottom: 0.9rem;
    }
    &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 100%;
        color: $white;
        padding: 0.5rem 0 2.5rem 0;
        text-align: right;
        @include tablet {
            width: 85.5%;
            padding: 2.5rem 3rem 2.5rem 0;
            text-align: left;
        }
        font-size: 1.15rem;
        line-height: 1.8rem;
        font-weight: $light;

    }
}
