.news {
    padding: 4rem 0;
    &__column {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 3rem;
        padding: 0 0.7rem;
        @include iphone {
            grid-template-columns: repeat(2, 1fr);
            grid-gap: 1.35rem;
            padding: 0;
        }
        @include laptop {
            grid-template-columns: repeat(3, 1fr);
            grid-gap: 1rem;
        }
        @include desktop {
            grid-gap: 3.35rem;
        }
    }
    &__col {
        width: 100%;
        display: flex;
        justify-content: center;
        margin: 0;
        padding: 0;
        @include desktop {
            padding: 0.5rem;
        }
        position: relative;
        margin-bottom: 2.5rem;
    }
    &__panel {
        background: $white;
        position: absolute;
        width: 86%;
        bottom: -5rem;
        @include iphone {
            bottom: -1.3rem;
        }
        margin: 0;
        padding: 0.4rem 1rem 1rem 1rem;
        @include desktop {
            padding: 0.4rem 1.5rem 1rem 1.5rem;
        }
        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0);
        transition: all 0.13s ease-in;
    }
    &__panel:hover {
        box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.2);
        bottom: -4.55rem;
        @include iphone {
            bottom: -0.55rem;
        }
    }
    &__panel:hover &__title-hover {
        border-bottom: 1px solid $black;
    }
    &__date {
        color: $primary-color;
        font-weight: $normal;
        font-size: 1.32rem;
        padding: 0.3rem 0 0 0;
    }
    &__title {
        line-height: 1.68rem;
        font-size: 1.18rem;
        min-height: auto;
        margin-top: -0.5rem;
        @include iphone {
            min-height: 92px;
            margin-top: 0;
        }
        @include laptop {
            font-size: 1.18rem;
            line-height: 1.68rem;
        }
        @include desktop-small {
            font-size: 1.25rem;
            line-height: 1.7rem;
        }
        @include desktop {
            font-size: 1.4rem;
            line-height: 1.9rem;
        }
    }
    &__img {
        object-fit: cover;
        height: 240px;
        width: 100%;
        @include iphone {
            height: 325px;
        }
        // @include desktop {
        //     height: auto;
        // }
    }
    &__img-link {
        width: 100%;
    }
}
.news-detail {
    padding: 5.5rem 0.8rem 4rem 0.8rem;
    @include iphone {
        padding: 5.5rem 0.8rem 4rem 0.8rem;
    }
    @include tablet {
        padding: 5.5rem 0.8rem 4rem 0.8rem;
    }
    @include laptop {
        padding: 5.5rem 8rem 4rem 8rem;
    }
    @include desktop-small {
        padding: 5.5rem 8rem 4rem 8rem;
    }
    &__date {
        font-size: 1.39rem;
        color: $black;
        font-weight: $medium;
        padding: 0 0 0.8rem 0;
        margin-bottom: 0;
    }
    &__column {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }
    &__col:nth-child(1) {
        width: 100%;
        @include tablet {
            width: 65%;
        }
    }
    &__col:nth-child(2) {
        width: 100%;
        @include tablet {
            width: 33%;
            margin-left: 2%;
        }
    }
    &__col-full-width {
        width: 100%;
    }
    &__btn {
        text-transform: unset;
        font-size: 0.9rem;
        font-weight: 400;
        letter-spacing: 0.07rem;
        margin-top: 1rem;
        margin-left: auto;
        margin-right: auto;
    }
    &__img {
        width: 100%;
    }
}