.grid-layout-2col {
    grid-template-columns: repeat(1, 1fr);
    @include tablet {
        padding: 2rem 0;
        grid-template-columns: repeat(2, 1fr);
    }
    &__col {
        height: auto;
        @include tablet {
            height: 580px;
        }
        @include laptop {
            height: 610px;
        }
        @include desktop {
            height: 650px;
        }
    }
}

.grid-layout {
    padding: 0;
    margin-bottom: 4rem;
    &__desktop {
        display: none;
        @include tablet {
            display: grid;
        }
    }
    &__mobile {
        display: grid;
        margin-top: 2.5rem;
        padding: 0 0.85rem;
        @include tablet {
            display: none;
            margin-top: 0;
            padding: 0;
        }
    }
    &__col {
        width: 100%;
        height: auto;
        margin-bottom: 2rem;
        @include tablet {
            margin-bottom: 0;
        }
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    &__col:nth-child(2) {
        padding: 0;
        @include tablet {
            padding: 2.7rem;
        }
        @include laptop {
            padding: 3.2rem;
        }
        @include desktop {
            padding: 5rem;
        }
    }
    &__col:nth-child(3) {
        padding: 0;
        @include tablet {
            padding: 2.7rem;
        }
        @include laptop {
            padding: 3.2rem;
        }
        @include desktop {
            padding: 5rem;
        }
    }
    &__col:nth-child(6) {
        padding: 0;
        @include tablet {
            padding: 2.7rem;
        }
        @include laptop {
            padding: 3.2rem;
        }
        @include desktop {
            padding: 5rem;
        }
    }
    &__col:nth-child(7) {
        padding: 0;
        @include tablet {
            padding: 2.7rem;
        }
        @include laptop {
            padding: 3.2rem;
        }
        @include desktop {
            padding: 5rem;
        }
    }
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__heading {
        margin-top: -0.8rem;
        margin-bottom: 0.5rem;
        text-align: center;
        @include iphone {
            text-align: left;
        }
        @include tablet {
            font-size: 1.9rem;
        }
        @include desktop {
            margin-bottom: 0.9rem;
            font-size: 2.28rem;
        }
    }
    &__subheading {
        margin-bottom: -0.5rem;
        @include desktop {
            margin-bottom: 0.9rem;
        }
    }
    &__content {
        line-height: 1.8rem;
        font-size: 1rem;
        @include tablet {
            line-height: 1.4rem;
            font-size: 0.9rem;
        }
        @include laptop {
            line-height: 1.6rem;
            font-size: 1rem;
        }
        @include desktop {
            line-height: 2.2rem;
            font-size: 1rem;
        }
    }
}