.project-summary {
    padding: 2.9rem 0 2.5rem 0;
    @include iphone {
        padding: 4.9rem 0 2.5rem 0;
    }
    &__container {
        display: flex;
        flex-wrap: wrap;
        width: 80%;
        margin-left: auto;
        margin-right: auto;
        @include laptop {
            width: 90%;
        }
        @include desktop-small {
            width: 80%;
        }
    }
    &__col {
        width: 100%;
        margin: 0 0 0.45rem 0;
        padding: 2.1rem 1rem;
        @include iphone {
            width: 48.5%;
            margin: 0 0.75% 1rem 0.75%;
            padding: 2.1rem 1.5rem;
        }
        @include laptop {
            width: 23.5%;
            margin: 0 0.75% 0 0.75%;
        }
        background: $primary-color;
        color: $white;
        text-align: center;
    }
    &__heading {
        font-family: $footer-font-family;
        font-weight: $bold;
        font-size: 1.4rem;
        line-height: 2.2rem;
        min-height: auto;
        margin-bottom: 0.7rem;
        @include iphone {
            font-size: 1.55rem;
            line-height: 2rem;
            min-height: 95px;
            margin-bottom: 0;
        }
        @include laptop {
            font-size: 1.3rem;
            line-height: 1.8rem;
        }
        @include desktop-small {
            font-size: 1.65rem;
            line-height: 2.3rem;
        }
        @include desktop {
            font-size: 2.1rem;
            line-height: 2.8rem;
        }
    }
    &__description {
        font-size: 0.95rem;
        line-height: 1.25rem;
        font-weight: $light;
    }
}