.footer-logos {
    padding: 4rem 3rem;
    @include iphone {
        padding: 4rem 4rem;
    }
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    &__img {
        width: 50%;
        margin-bottom: 1rem;
        @include iphone {
            width: 25%;
            margin-bottom: 0;
        }
    }
    &__container {
        max-width: 900px;
        display: flex;
        flex-wrap: wrap;
    }
}