.sector-two-column {
    padding: 0;
    @include tablet {
        padding: 3.2rem 3.2rem 4rem 3.2rem;
    }
    @include laptop {
        padding: 7rem 7rem 4rem 7rem;
    }
    color: $white;
    &__wrapper {
        max-width: 1590px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column-reverse;
        padding-left: 0;
        @include tablet {
            padding-left: 0;
            flex-direction: row;
            text-align: left;
        }
        @include laptop {
            padding-left: 0;
        }
        @include desktop-small {
            padding-left: 2.8rem;
        }
    }
    &__title {
        font-size: 1.49rem;
        text-align: center;
        @include iphone {
            font-size: 1.99rem;
            line-height: 2.5rem;
            margin-bottom: 0.4rem;
            text-align: left;
        }
    }
    &__subtitle {
        font-size: 1.25rem;
        line-height: 1.9rem;
        letter-spacing: 0.02rem;
        margin-bottom: 0.5rem;
        @include iphone {
            font-size: 1.3rem;
            margin-bottom: 0.5rem;
        }
        @include tablet {
            margin-bottom: 0;
        }
    }
    &__col {
        width: 100%;
        @include desktop-small {
            width: 50%;
        }
    }
    &__col:nth-child(1) &__col-wrap {
        background: $primary-color;
        padding: 2rem 2.5rem 3rem 2.5rem;
        @include iphone {
            padding: 2rem 3.5rem 3rem 3rem;
        }
        @include desktop-small {
            padding: 3rem 5.5rem 4rem 4rem;
        }
    }
    &__col:nth-child(2) &__img {
        margin-top: 0;
        margin-left: 0;
        object-fit: cover;
        height: auto;
        margin-bottom: -2px;
        @include tablet {
            margin-top: 1rem;
            height: auto;
            margin-bottom: 0;
        }
        @include laptop {
            height: auto;
        }
        @include desktop-small {
            margin-top: 8rem;
            margin-left: -2.5rem;
            height: 62vw;
            max-height: 740px;
        }
        @include desktop {
            margin-top: 6rem;
            margin-left: -2.5rem;
            height: 32vw;
            max-height: 565px;
        }
    }
}