.contact-page {
    padding: 2.2rem 0 4rem 0;
    @include laptop {
        padding: 4rem 0;
    }
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
    }
    &__col:nth-child(1) {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        margin-right: 0%;
        @include laptop {
            width: 54%;
            margin-right: 3%;
        }
    }
    &__col:nth-child(1) &__col-2:last-of-type {
        margin-top: 1.5rem;
        @include laptop {
            margin-top: 2.5rem;
        }
    }
    // &__col:nth-child(1) &__col-2:nth-child(1) {
    //     margin-top: 1rem;
    //     @include laptop {
    //         margin-top: 0;
    //     }
    // }
    &__col:nth-child(2) {
        width: 100%;
        padding: 2.9rem 1.8rem 3.8rem 1.8rem;
        @include laptop {
            width: 43%;
            padding: 2.9rem 1.8rem 1.8rem 1.8rem;
        }
        @include laptop {
            padding: 3.9rem 1.8rem 1.8rem 1.8rem;
        }
        background: $primary-color;
        color: $white;
        text-align: center;
    }
    &__col-2 {
        width: 100%;
        @include desktop-small {
            width: 50%;
        }
    }
    &__content {
        padding: 0 0 0.5rem 0;
        font-weight: $light;
    }
    &__content-mb-20 {
        margin-bottom: 0;
        @include desktop-small {
            margin-bottom: 1.1rem;
        }
        @include desktop {
            margin-bottom: 2rem;
        }
    }
    &__content-bold {
        padding: 0 0 0.5rem 0;
        font-weight: $bold;
        
    }
    &__heading {
        color: $white;
        font-family: $footer-font-family;
        text-align: center;
        font-size: 2.3rem;
        line-height: 2rem;
        padding: 0 0 0.7rem 0;
        margin-top: 0;
        margin-bottom: 1rem;
        font-weight: 700;
    }
    &__subheading {
        font-size: 1.1rem;
        @include laptop {
            font-size: 1.3rem;
        }
        line-height: 1.65rem;
        margin-top: -0.5rem;
        max-width: 370px;
        margin-left: auto;
        margin-right: auto;
    }
    &__submit {
        max-width: 100%;
        margin-bottom: 2.5rem;
        @include iphone {
            margin-bottom: 2rem;
        }
        @include laptop {
            margin-bottom: 0;
        }
        @include desktop-small {
            max-width: 245px;
        }
        background: $white;
        color: $black;
        margin-left: auto;
        margin-right: auto;
        font-weight: $semi-bold;
    }
    &__map {
        margin-top: 2rem;
        margin-bottom: 2rem;
        @include laptop {
            margin-bottom: 0;
        }
    }
}