.contact {
    &__heading {
        color: $white;
        font-family: $footer-font-family;
        text-align: center;
        font-size: 1.65rem;
        @include iphone {
            font-size: 2.3rem;
        }
        line-height: 2rem;
        padding: 0 0 0.7rem 0;
        margin-top: -3.5rem;
        margin-bottom: 1rem;
        font-weight: 700;
        @include tablet {
            margin-top: -3rem;
        }
        @include laptop {
            margin-top: 0;
        }
    }
    &__subheading {
        color: $white;
        text-align: center;
        font-size: 1.15rem;
        line-height: 1.3rem;
        max-width: 90%;
        margin-left: auto;
        margin-right: auto;
        @include iphone {
            font-size: 1.15rem;
            line-height: 1.5rem;
            max-width: 400px;
        }
        font-weight: $light;
    }
    &__column {
        margin-top: 2rem;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -3rem;
        @include tablet {
            margin-bottom: -3rem;
        }
        @include laptop {
            margin-bottom: 0;
        }
    }
    &__col:nth-child(1) {
        width: 100%;
        margin-right: 0;
        @include tablet {
            width: 49%;
            margin-right: 1%;
        }
    }
    &__col:nth-child(2) {
        width: 100%;
        margin-right: 0;
        @include tablet {
            width: 49%;
            margin-left: 1%;
        }
    }
    &__col:nth-child(3) {
        width: 100%;
        margin-right: 0;
        @include tablet {
            width: 49%;
            margin-right: 1%;
        }
    }
    &__col:nth-child(4) {
        width: 100%;
        margin-right: 0;
        @include tablet {
            width: 49%;
            margin-left: 1%;
        }
    }
    &__input {
        width: 100%;
        padding: 0.4rem 0.9rem;
        margin: 0 0 0.59rem 0;
        @include iphone {
            padding: 0.89rem 1.2rem;
            margin: 0 0 0.99rem 0;
        }
        color: $black;
        background: $white;
        font-size: 0.9rem;
        border-radius: 0;
    }
    &__input:focus-visible {
        outline: 1px solid $primary-color;
    }
    &__input::placeholder { 
        color: $black;
        opacity: 1;
        }
    &__textarea {
        height: 6rem;
        @include desktop-small {
            height: 12.6rem;
        }
        margin-bottom: 0.4rem;
    }
    &__textarea-home {
        height: 9rem;
        @include iphone {
            height: 12.3rem;
        }
        @include tablet {
            height: 12.5rem;
        }
        @include laptop {
            height: 12.6rem;
        }
        margin-bottom: 0.4rem;
    }
    &__select {
        width: 100%;
        padding: 0.9rem 0.9rem;
        @include iphone {
            padding: 1.21rem 1.2rem;
        }
        color: $black;
        background: $white;
        font-size: 0.9rem;
        -webkit-appearance: none;
        background-size: 9px;
        @include iphone {
            background-size: 10px;
        }
        @include tablet {
            background-size: 10px;
        }
        @include laptop {
            background-size: 11px;
        }
        margin: 0 0 0.8rem 0;
        background-image: url('/wp-content/themes/fluidpress/images/select-menu-arrow.png');
        background-repeat: no-repeat;
        top: 50%;
        background-position: 95% 50%;
    }
    &__select:focus-visible {
        outline: 1px solid $primary-color;
    }
    &__select::placeholder { 
        color: $black;
        opacity: 1;
        }
    &__submit {
        width: 100%;
        padding: 0.89rem 1.2rem;
        font-weight: $normal;
        -webkit-appearance: none;
        display: block;
        @include iphone {
            margin-top: 0;
            font-weight: $light;
        }
        color: $white;
        background: $primary-color;
        font-size: 0.9rem;
        letter-spacing: 0.01rem;
        text-transform: uppercase;
        border-radius: 0;
        cursor: pointer;
    }
    &__upload {
        display: none;
        color: $white;
        font-size: 0.9rem;
        line-height: 1.1rem;
    }
    &__upload-btn {
        color: $white;
        max-width: 185px;
        width: 185px;
        padding: 1rem 0 1.3rem 0;
        @include iphone {
            padding: 1rem 0 2.5rem 0;
        }
        box-shadow: none;
    }
    &__upload-btn::-webkit-file-upload-button {
        visibility: hidden;
        @include tablet {
            visibility: visible;
        }
    }
    &__upload-btn::before {
        content: 'Upload your file';
        color: $primary-color;
        display: inline-block;
        background: $white;
        border: none;
        padding: 5px 0;
        outline: none;
        white-space: nowrap;
        -webkit-user-select: none;
        cursor: pointer;
        font-weight: $bold;
        text-transform: uppercase;
        letter-spacing: 0.02rem;
        font-size: 10pt;
        width: 190px;
        text-align: center;
        position: absolute;
    }
    &__upload-btn:active {
        outline: none;
    }
    &__upload-btn:hover::before {
        border-color: $pale-grey-background;
    }
    &__upload-btn:active::before {
        background-color: $pale-grey-background; 
    }
}